* {
    list-style: none;
    padding: 0;
    margin: 0;
    font-family: 'Plus Jakarta Sans';
}
.container {
    width: 1216px;
    padding: 0 15px;
    margin: 0 auto;
}
@font-face {
    font-family: 'Plus Jakarta Sans';
    src: url(../Fonts/PlusJakartaSans-Regular.ttf);
}
.section2 {
    padding-top: 100px;
    padding-bottom: 150px;
}
