* {
    list-style: none;
    padding: 0;
    margin: 0;
    font-family: 'Plus Jakarta Sans';
}
.container {
    width: 1216px;
    padding: 0 15px;
    margin: 0 auto;
}
@font-face {
    font-family: 'Plus Jakarta Sans';
    src: url(../../Fonts/PlusJakartaSans-Regular.ttf);
}
header {
    padding-top: 92px;
    padding-bottom: 170px;
}
.header_display-flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header_texts {
    width: 550px;
}
h1 {
    padding-bottom: 20px;
    font-size: 56px;
    font-weight: 700;
    line-height: 78px;
    letter-spacing: 0px;
    text-align: left;
}
.header_p {
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0px;
    text-align: left;
    color: #636F87;
    padding-bottom: 40px;
}
.header_btn {
    font-size: 16px;
    font-weight: 700;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: center;
    color: #FFFFFF;
    width: 168px;
    height: 59px;
    border-radius: 6px;
    background: #1AAB7B;
    border: none;
    transition: 0.3s;
    cursor: pointer;
}
.header_btn:hover {
    color: #1AAB7B;
    border: 1px solid #1AAB7B;
    transition: 0.3s;
    background-color: transparent;
}