* {
    list-style: none;
    padding: 0;
    margin: 0;
    font-family: 'Plus Jakarta Sans';
}
.container {
    width: 1216px;
    padding: 0 15px;
    margin: 0 auto;
}
@font-face {
    font-family: 'Plus Jakarta Sans';
    src: url(../Fonts/PlusJakartaSans-Regular.ttf);
}
footer {
    padding-top: 100px;
    background-color: #101010;
}
.footer_display_flex {
    display: flex;
    padding-bottom: 123px;
}
.footer_ul_div {
    padding-right: 151px;
}
.footer_h6 {
    color: #FFFFFF;
    font-size: 20px;
    font-weight: 700;
    text-align: left;
    margin-bottom: 30px;
}
.footer_ul1 {
    display: flex;
    flex-direction: column;
    row-gap: 26px;
}
.footer_ul_li {
    color: #A1A8B7;
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
    text-align: left;
    text-decoration: none; 
    transition: 0.2s;
}
.footer_ul_li:hover {
    color: #FFFFFF;
    transition: 0.2s;
}
.footer_ul_div3 {
    padding-left: 521px;
}
.footer_all_svg {
    padding-top: 32px;
    display: flex;
    align-items: center;
    column-gap: 40px;
}
.footer_all_svg svg {
    fill: white;
    transition: 0.3s;
}
.footer_all_svg svg:hover {
    transition: 0.3s;
    fill: #A1A8B7;
    cursor: pointer;
}
hr {
    border: 1px solid #3C4155;
}
.footer_displayFlex {
    padding-top: 50px;
    padding-bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.footer_displayFlex h6 {
    color: #FFFFFF;
    font-size: 18px;
    font-weight: 500;
    text-align: right;
}