* {
    list-style: none;
    padding: 0;
    margin: 0;
    font-family: 'Plus Jakarta Sans';
}
.container {
    width: 1216px;
    padding: 0 15px;
    margin: 0 auto;
}
@font-face {
    font-family: 'Plus Jakarta Sans';
    src: url(../Fonts/PlusJakartaSans-Regular.ttf);
}
.section3 {
    padding-top: 150px;
    padding-bottom: 200px;
}
.section3_h3 {
    color: #101010;
    font-size: 36px;
    font-weight: 700;
    line-height: 50px;
    text-align: center;
}
.section3_p {
    color: #636F87;
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    text-align: center;
    padding-top: 16px;
    padding-bottom: 60px;
}
.section3_bigDiv {
    background: #F2F4F6;
}
.sectipn3_inputs_div {
    padding-top: 64px;
    padding-bottom: 64px;
    padding-left: 312px;
    padding-right: 312px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.section3_inputs {
    display: flex;
    flex-direction: column;
    row-gap: 51px;
    padding-bottom: 40px;
}
.section3_inputs_input {
    border-top: none;
    border-left: none;
    border-right: none;
    background-color: transparent;
    border-bottom: 1.6px solid #CFD2D7;
    width: 384px;
    height: 46px;
}
.section3_inputs_input::placeholder {
    color: #101010;
    font-size: 20px;
    font-weight: 700;
    line-height: 25px;
    text-align: left;
}
.section3_select {
    border-bottom: 2px solid #CFD2D7;
    border-top: none;
    border-left: none;
    border-right: none;
    background-color: transparent;
    width: 384px;
    height: 46px;
    color: #101010;
    font-size: 20px;
    font-weight: 700;
    line-height: 25px;
    text-align: left;
}
.section3_options {
    color: #101010;
    font-size: 20px;
    font-weight: 700;
    line-height: 25px;
    text-align: left;
}
.section3_btn {
    color: #FFFFFF;
    font-size: 16px;
    font-weight: 700;
    line-height: 19px;
    text-align: center;
    background: #1AAB7B;
    width: 180px;
    height: 59px;
    border-radius: 6px;
    border: none;
    transition: 0.3s;
    cursor: pointer;
}
.section3_btn:hover {
    transition: 0.3s;
    border: 2px solid #1AAB7B;
    background-color: transparent;
    color: #1AAB7B;
}
.section3_h6 {
    color: #858EA1;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    padding-top: 24px;
}