* {
    list-style: none;
    padding: 0;
    margin: 0;
    font-family: 'Plus Jakarta Sans';
}
.container {
    width: 1216px;
    padding: 0 15px;
    margin: 0 auto;
}
@font-face {
    font-family: 'Plus Jakarta Sans';
    src: url(../../Fonts/PlusJakartaSans-Regular.ttf);
}
.section1 {
    padding-bottom: 50px;
    padding-top: 50px;
    background: #F2F4F6;
}
h2 {
    font-size: 36px;
    font-weight: 600;
    text-align: center;
    color: #101010;
    padding-bottom: 40px;
}
.section1_1-div {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.section1_1-div_card1 {
    width: 384px;
    height: 358px;
    border-radius: 6px;
    background: #1AAB7B;
    transition: 0.3s
}
.section1_1-div_card1:hover {
    background-color: transparent;
    color: #101010;
    border: 2px solid #E1E7ED;
    transition: 0.3s
}
.section1_1-div_card1_difplay_flex1 {
    display: flex;
    align-items: center;
    column-gap: 43px;
    padding-top: 30px;
    padding-bottom: 11px;   
}
.section1_1-div_card1_difplay_flex1 h3 {
    font-size: 32px;
    font-weight: 600;
    line-height: 45px;
    text-align: left;
    color: #FFFFFF;
}
