* {
    list-style: none;
    padding: 0;
    margin: 0;
    font-family: 'Plus Jakarta Sans';
}
.container {
    width: 1216px;
    padding: 0 15px;
    margin: 0 auto;
}
@font-face {
    font-family: 'Plus Jakarta Sans';
    src: url(../../Fonts/PlusJakartaSans-Regular.ttf);
}
nav {
    padding-top: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid #EEEEF4;
}
.nav_display-flex {
    display: flex;
    align-items: center;
}
.nav_ul {
    padding-right: 527px;
    padding-left: 50px;
    display: flex;
    align-items: center;
    column-gap: 30px;
}
.nav_ul a {
    text-decoration: none;
    color: #101010;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: 0px;
    text-align: left;
    font-weight: 700;
    transition: 0.1s;
}
.nav_ul a:hover {
    color: #00AF4F;
    transition: 0.1s;
    
}
.nav_btn {
    background: #101010;
    width: 156px;
    height: 41px;
    border-radius: 6px;
    color: white;
    border: none;
    font-weight: 700;
    cursor: pointer;
    transition: 0.3s;
}
.nav_btn:hover {
    color: #101010;
    background-color: transparent;
    border: 1px solid black;
    transition: 0.3s;
}